@font-face {
  font-family: 'Magneto';
  src: url('/fonts/magneto/magneto-bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Ubuntu Mono';
  src:
    url('/fonts/ubuntu-mono/ubuntu-mono.woff2') format('woff2'),
    url('/fonts/ubuntu-mono/ubuntu-mono.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Ubuntu Mono';
  src:
    url('/fonts/ubuntu-mono/ubuntu-mono-italic.woff2') format('woff2'),
    url('/fonts/ubuntu-mono/ubuntu-mono-italic.woff') format('woff');
  font-weight: 400;
  font-style: italic;
}

@font-face {
  font-family: 'Ubuntu Mono';
  src:
    url('/fonts/ubuntu-mono/ubuntu-mono-bold.woff2') format('woff2'),
    url('/fonts/ubuntu-mono/ubuntu-mono-bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Ubuntu Mono';
  src:
    url('/fonts/ubuntu-mono/ubuntu-mono-bold-italic.woff2') format('woff2'),
    url('/fonts/ubuntu-mono/ubuntu-mono-bold-italic.woff') format('woff');
  font-weight: 700;
  font-style: italic;
}
